/* Container for password input and the icon */
/* src/assets/styles/login.css */

/* Container for password input and the icon */
.password-container {
  position: relative;
}

/* Password eye icon */
.password-icon {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}
