/* Main page layout */
.employees-page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* Content layout: Sidebar and main content */
.main-layout {
  display: flex;
  margin-top: 60px; /* Adjust according to your navbar height */
  padding-left: 250px; /* To ensure content doesn't overlap the sidebar */
}

/* Main content styles */
.main-content {
  flex: 1;
  padding: 20px;
  background-color: #f4f4f4;
  margin-right: px;
  margin-right: 100px;
}

.content-container {
  padding: 20px;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}

/* Add Employee Section */
.add-employee-section {
  margin-bottom: 30px;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.add-employee-section h3 {
  margin-bottom: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group input {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-group button {
  padding: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.form-group button:hover {
  background-color: #45a049;
}

/* Table Styling */
.table-container {
  margin-top: 20px;
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 30px;
}

th,
td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #f1f1f1;
}

button {
  padding: 6px 12px;
  margin: 0 5px;
  background-color: #4caf50;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

button:hover {
  background-color: #45a049;
}

/* Chart container */
.chart-container {
  margin-top: 30px;
  width: 100%;
  max-width: 900px;
  margin: 0 auto;
}

/* Sidebar styling */
.sidebar {
  width: 250px;
  background-color: #333;
  color: white;
  padding: 20px;
  position: fixed;
  top: 60px; /* Adjust based on navbar height */
  left: 0;
  height: 100vh; /* Make sure the sidebar takes full height */
  overflow-y: auto;
}

.sidebar a {
  color: white;
  text-decoration: none;
  display: block;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 4px;
}

.sidebar a:hover {
  background-color: #575757;
}

/* Navbar Styles */
.navbar-dark {
  background-color: #333;
  padding: 10px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.navbar-section {
  display: flex;
  align-items: center;
}

.navbar-logo img {
  max-height: 40px;
  margin-right: 20px;
}

.navbar-left {
  flex-grow: 1;
}

.navbar-title {
  color: white;
  text-decoration: none;
  font-size: 18px;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 8px;
}

.search-zone {
  display: flex;
  align-items: center;
}

.search-input {
  padding: 8px;
  border: none;
  border-radius: 4px;
}

.search-button {
  padding: 8px 12px;
  border: none;
  background-color: #28a745;
  color: white;
  cursor: pointer;
  border-radius: 4px;
  margin-left: 10px;
}

.navbar-icons {
  display: flex;
  gap: 20px;
}

.navbar-icon {
  color: white;
  cursor: pointer;
}

.navbar-icon:hover {
  color: #f1f1f1;
}
