/* src/assets/styles/dashboard.css */

.dashboard-page {
  display: flex;
  flex-direction: column;
  height: 100vh; /* Enables flexbox for the overall layout */
}

.dashboard-content {
  display: flex;
  flex: 1;
}

.main-content {
  flex: 1;
  padding: 20px;
  background-color: transparent; /* ← No background */
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  margin-left: 220px;
}

p {
  font-size: 1rem; /* Set font size for paragraph text */
  color: #555; /* Light gray color for the paragraph text */
}

body {
  margin: 0;
  font-family: Arial, sans-serif;
}

.dashboard {
  display: flex;
  flex-wrap: wrap; /* Allow wrapping for cards */
  justify-content: flex-start; /* Align cards to the left */
  gap: 10px; /* Add space between cards */
  padding: 10px; /* Padding around the cards */
  margin-top: 50px;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  margin: 10px;
  width: 250px;
  margin-left: 70px; /* Smaller width for the cards */
}

.card:hover {
  transform: scale(1.05);
}

.card h3 {
  color: #fff;
  margin-bottom: 8px;
}

.card p {
  color: #fff;
  font-size: 12px;
}

.card strong {
  color: #fff;
  font-size: 18px;
}

/* Pie Chart Container */
.pie-chart-container {
  margin-top: 20px;
  margin-left: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 40%; /* Ensure it takes up full width */
}

.pie-chart-container h3 {
  margin-bottom: 20px;
  font-size: 20px;
  color: #333;
}

/* Wrapper for the Pie Chart to control its size */
.pie-chart-wrapper {
  width: 500px; /* Set the width for the pie chart (smaller size) */
  height: 500x; /* Set the height for the pie chart (smaller size) */
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Main Container */
.app-container {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* Blue Border Container */
.blue-border {
  border: 4px solid #007bff; /* Blue color */
  padding: 10px;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  gap: 15px; /* Space between pills */
}

/* Pill Button */
.pill-button {
  background-color: #7b5cff; /* Purple color */
  color: white;
  padding: 9px 18px;
  margin-top: 10px;
  border-radius: 25px; /* Rounded corners */
  text-align: center;
  font-size: 16px;
  font-weight: 500;
  margin-left: 80px;
  margin-right: 80px;
}

/* Read More Section */
.read-more {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  margin-left: 50px;
}

.read-more span {
  font-size: 18px;
  font-weight: 600;
}

.plus-sign {
  font-size: 24px;
  font-weight: bold;
  color: #7b5cff; /* Match pill button color */
}
/* src/assets/styles/dashboard.css */
.admin-services-page {
  display: flex;
}

.main-content {
  margin-left: 220px; /* Sidebar space */
  padding: 20px;
  background-color: #f4f6f9;
  min-height: 100vh;
}

h1 {
  font-size: 32px;
  color: #333;
}

.services-container {
  display: flex;
  gap: 20px;
  margin-top: 30px;
  flex-wrap: wrap;
}

.service-card {
  background-color: #ffffff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  flex: 1 1 200px; /* Flexible card width */
  text-align: center;
  min-width: 200px;
}

.service-card h3 {
  font-size: 20px;
  color: #007bff;
}

.service-card p {
  color: #555;
}

.service-card .btn {
  margin-top: 10px;
  background-color: #007bff;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.service-card .btn:hover {
  background-color: #0056b3;
}
