/* Sidebar Styles */
.admin-services-sidebar {
  width: 250px; /* Width of the sidebar */
  background-color: #333; /* Dark blue background */
  color: white;
  padding: 20px;
  height: calc(100vh - 50px); /* Full height minus navbar height */
  position: fixed; /* Fixed positioning */
  top: 50px; /* Align it below the navbar */
  left: 0;
  display: flex;
  flex-direction: column; /* Stack items vertically */
  overflow-y: auto; /* Scroll if content exceeds the height */
  z-index: 1000;
}

/* Optional: Hover effects for links */
.admin-services-sidebar .sidebar-item {
  font-size: 1rem;
  padding: 10px 0;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: background-color 0.3s ease;
  margin-bottom: 10px;
}

.admin-services-sidebar .sidebar-item:hover {
  background-color: #5d7a98; /* Darker blue when hovered */
}

.admin-services-sidebar .sidebar-item a {
  color: white;
  text-decoration: none;
  display: flex;
  align-items: center;
  width: 100%;
}

.admin-services-sidebar .sidebar-item i {
  margin-right: 10px;
}

.admin-services-sidebar .sidebar-item svg {
  font-size: 20px; /* Icon size */
}

/* Media query for smaller screens */
@media (max-width: 768px) {
  .admin-services-sidebar {
    width: 200px; /* Reduced width on smaller screens */
  }
}
